<script>
  import { afterUpdate } from "svelte";
  import { _, locale } from "svelte-i18n";
  import Carousel from "./Carousel.svelte";
  import { defaultLocale } from "../i18n.js";

  export let id;
  export let name;
  export let region;
  export let country;
  export let arrivalDate;
  export let photoCount;
  // export let type;

  const typeToIconMap = {
    "old-town": "storefront",
    city: "location_city",
    mountain: "landscape",
    nature: "nature"
  };

  const lang = $locale.substr(0, 2);

  let nameEl;
  let localeName;

  afterUpdate(() => {
    localeName = name[lang] || name[defaultLocale];
    adaptNameFonSize(localeName, lang);
  });

  function adaptNameFonSize(name, lang) {
    const nameLength = name.length;
    let fontSize = 48;

    switch (lang) {
      case "zh":
        if (nameLength > 6) {
          fontSize = 26;
        } else if (nameLength > 4) {
          fontSize = 36;
        }
        break;
      default:
        if (nameLength > 20) {
          fontSize = 23;
        } else if (nameLength > 18) {
          fontSize = 24;
        } else if (nameLength > 15) {
          fontSize = 28;
        } else if (nameLength > 12) {
          fontSize = 34;
        } else if (nameLength > 9) {
          fontSize = 38;
        }
    }
    nameEl.style.fontSize = fontSize + "px";
  }
</script>

<style>
  .place-card {
    max-width: 900px;
    position: relative;
    background-color: #ffefd6;
    width: calc(100% - 28px);
    margin: 5vh auto 0 auto;
    padding: 0 1vw 1vw 1vw;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  }

  .card-header {
    display: flex;
    justify-content: space-between;
    padding: 12px 8px 4px 8px;
  }

  .card-body {
    max-height: 60vh;
  }

  .name {
    font-size: 48px;
    line-height: 90px;
  }

  .details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 18px;
    line-height: 36px;
    text-align: right;
  }

  @media (orientation: landscape) {
    .place-card {
      width: 65vw;
      max-height: calc(90vh - 1vw);
      margin: 5vh 0 5vh 5vh;
    }
    .card-body {
      max-height: 70vh;
    }
  }
</style>

<div class="place-card">
  <div class="card-header">
    <div bind:this={nameEl} class="name">{localeName}</div>
    <div class="details">
      <div class="arrival-date">
        {arrivalDate.toLocaleDateString($locale, {})}
      </div>
      <div class="region">
        {region ? $_('regions.' + region) + $_('regions.suffix') : ''}
        {#each country as c, i}{i > 0 ? '-' : '' + $_('countries.' + c)}{/each}
      </div>
      <!-- <div class="stay-duration">2 mois</div> -->
      <!-- <div class="type">
        <span class="material-icons">{typeToIconMap[type]}</span>
      </div> -->
    </div>
  </div>
  <div class="card-body">
    {#if photoCount > 0}
      <Carousel {photoCount} folderName={id} altText={name} on:nextClicked />
    {/if}
  </div>
</div>
